export default {
	onLoad(e) {
		this.tempFace = e.img;
	},
	data() {
		return {
			tempFace: ''
		};
	},
	methods: {
		// 重新选择头像
		changePendingFace() {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album'],
				success: res => {
					let img = res.tempFilePaths[0];
					this.tempFace = img;
				}
			});
		},
		upload() {
			let user = uni.getStorageSync('user')
			uni.showLoading({
				mask: true,
				title: '上传中...'
			})
			uni.uploadFile({
				url: `${this.$api}/user/uploadFace?userId=${user.id}&qq=7929290`,
				filePath: this.tempFace,
				name: 'file',
				header: {
					"headerUserId": user.id,
					"headerUserToken": user.userUniqueToken
				},
				success: (response) => {
					// 	console.log(res.data)
					let res = JSON.parse(response.data)
					if (res.status === 200) {
						uni.setStorageSync('user', res.data)
						uni.showToast({
							title: "上传成功",
							icon:"success",
							duration: 2000
						})
						uni.navigateBack({
							delta:2
						})
					} else if (res.status === 502 || res.status === 500) {
						uni.showToast({
							title: res.data.msg,
							image: "../../static/icos/error.png"
						})
					}
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		}
	}
};
